<template>
	<div class="body">
		<div class="body_header flex_start">
			<i class="icon iconfont icon-touziren"></i>
			<span>投资人概况</span>
		</div>
		<div class="search_group">
			<div class="search_group_bottom flex_start">
				<div class="search_box">
					<el-input placeholder="投资人名称" v-model="name" clearable prefix-icon="el-icon-search">
					</el-input>
				</div>
				<div class="search_box">
					<el-input placeholder="IR" v-model="ir" clearable>
					</el-input>
				</div>
				<div class="search_box">
					<el-input placeholder="客户经理" v-model="manager" clearable>
					</el-input>
				</div>
				<div class="btn_box">
					<button @click="getInvesterManageList(1)">搜索</button>
				</div>
			</div>
		</div>
		<div class="line_box">
			<div class="line"></div>
		</div>
		
		<div class="main under_fund">
			<div class="under_fund_header flex_between">
				<div class="">
					投资人总数<span>(&nbsp;{{ total }}&nbsp;)</span>
				</div>
				<div class="flex_end">
					<button class="hover_btn" @click="setUserPower(0)">
						设置权限				
					</button>
					<button class="hover_btn flex_center" @click="downloadExcel">
						<span class="iconfont icon-daoru"></span>
						导出excel表格					
					</button>
					<button class="hover_btn flex_center" @click="downloadTemplet">
						<span class="iconfont icon-daoru"></span>
						下载投资人信息模板
					</button>
					<div class="">
						<label for="upload-fund-input">
							<div class="table-group-title-btn btn-blue-border">
								<span class="iconfont icon-daochu"></span>导入投资人信息
							</div>
						</label>
						<input type="file" ref="inputer" id="upload-fund-input" style="display:none" v-on:change="uploadManageCompanyInfoExcel($event)" />
					</div>
					<button class="under_fund_header_btn" @click="addNewInvester">
						<span class="iconfont icon-jia"></span>
						添加新的投资人
					</button>
				</div>
			</div>
			<div class="under_fund_main table">
				<el-table :data="tableData" border style="width: 100%" @selection-change="handleSelectionChange">
					<el-table-column type="selection" width="55">
    					</el-table-column>
    					<el-table-column sortable prop="" label="序号" width="80">
    						<template slot-scope="scope">
							{{ (scope.$index + 1) + (currentPage - 1) * pageSize }}
						</template>
					</el-table-column>
					<el-table-column sortable prop="name" label="投资人名称" width="180">
					</el-table-column>
					<el-table-column sortable prop="id" label="ID" width="80">
					</el-table-column>
					
					<el-table-column sortable prop="company" label="投资人所属公司" width="250">
					</el-table-column>
					<el-table-column sortable prop="cell" label="手机号" width="120">
					</el-table-column>
					<el-table-column sortable prop="manager" label="客户端账号" width="120">
						<template slot-scope="scope">
							{{ scope.row.open_status == 1?'已开启':'未开启' }}
						</template>
					</el-table-column>
					<el-table-column sortable prop="email" label="邮箱" width="150">
					</el-table-column>
					<el-table-column sortable prop="relate_investor" label="关联人" width="150">
					</el-table-column>
					<el-table-column sortable prop="ir_name" label="IR" width="120">
					</el-table-column>
					<el-table-column sortable prop="manager_name" label="客户经理" width="130">
					</el-table-column>
					<el-table-column sortable prop="subscribe_num" label="认缴数量" width="130">
					</el-table-column>
					<el-table-column sortable prop="rmb_subscribe_money" label="人民币认缴（万元）" width="180">
					</el-table-column>
					<el-table-column sortable prop="rmb_real_fee" label="人民币实缴（万元）" width="180">
					</el-table-column>
					<el-table-column sortable prop="rmb_return_fee" label="人民币返本（万元）" width="180">
					</el-table-column>
					<el-table-column sortable prop="rmb_income" label="人民币收益（万元）" width="180">
					</el-table-column>
					<el-table-column sortable prop="dollar_subscribe_money" label="美元认缴（万元）" width="180">
					</el-table-column>
					<el-table-column sortable prop="dollar_real_fee" label="美元实缴（万元）" width="180">
					</el-table-column>
					<el-table-column sortable prop="dollar_return_fee" label="美元返本（万元）" width="180">
					</el-table-column>
					<el-table-column sortable prop="dollar_income" label="美元收益（万元）" width="180">
					</el-table-column>
					<el-table-column sortable prop="irr" label="XIRR" width="120">
					</el-table-column>
					<el-table-column fixed="right" label="操作" width="250">
						<template slot-scope="scope">
							<el-button @click="setUserPower(scope.row)" type="text" size="small">设置权限</el-button>
							<el-button @click="toRunPage(scope.row)" type="text" size="small">投资详情</el-button>
							<el-button @click="view(scope.row)" type="text" size="small">查看</el-button>
							<el-button @click="deleteInvester(scope.row)" type="text" size="small">删除</el-button>
						</template>
					</el-table-column>
					<template slot="empty">
						<div class="table-nodata flex_start">
							<img src="../../images/table_nodata.png">
							<span>没有对应数据</span> 
						</div>
					</template>
				</el-table>
				<el-pagination @size-change="everyPageSizeChange" prev-text="上一页" next-text="下一页" background @current-change="changeCurrentPage" :current-page="currentPage" :page-size="pageSize" layout="sizes, prev, pager, next, jumper" :total="total">
    				</el-pagination>
			</div>
		</div>
		
		<!--  设置权限弹窗开始-->
		<el-dialog title="设置可见人员" :visible.sync="isSetPowerDialogShow" width="813px" center :append-to-body='true'>
			<set-power v-bind:have-power-user-ids="havePowerUserIds" v-on:get-set-power-id="getSetPowerIds"></set-power>
			<div slot="footer" class="dialog_footer clear">
				<button class="dialog_btn dialog_btn_cancel" @click="isSetPowerDialogShow=false">取消</button>
				<button class="dialog_btn dialog_btn_submit" @click="setPowerSubmit">确定</button>
			</div>
		</el-dialog>
		<!--设置权限弹窗结束-->
	</div>
</template>

<script>
	import setPower from '../../components/setPower.vue'
	import { dialogConfirm, dialogConfirmHasbtn } from '../../config/mUtils'
	import { getInvesterManageListApi,getUserlistApi,deleteInvesterApi,downloadTempletInvesterApi,downloadExcelInvesterApi,uploadInvestorExcelApi,getHavePowerUsersApi,setPowerSubmitApi } from '../../config/api'
	
	export default {
		name: 'investerManageList',
		data() {
			return {
				tableData: [],
				total: 0,
				name: '',
				ir: '',
				manager: '',
				currentPage: 1,
				pageSize: 10,
				noPowerTest: '您没有查看此内容的权限，请联系管理员。',
				
				isSetPowerDialogShow: false,
				havePowerUserIds: [],
				subComponentPostIds: [],
				ids: '', //设置权限时选择的用户ids
				is_multiple_set_power: 0,
				multiple_set_power_ids: '',
			}
		},
		created() {
			this.getInvesterManageList();
		},
		mounted() {},
		components: {
			setPower
		},
		methods: {
			getInvesterManageList(is_init_page){
				if (is_init_page) {
					this.currentPage = 1;
				}
				getInvesterManageListApi({
					page: this.currentPage,
					pageSize: this.pageSize,
					name: this.name,
					ir: this.ir,
					manager: this.manager
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.tableData = res.data.payload.data;
						this.total = res.data.payload.total;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			view(e){
				if (e.auth.check_operate) {
					this.$router.push({
						name: 'investerDetail',
						query: { id: e.id }
					})
				} else{
					dialogConfirm("info", this.noPowerTest);
				}
			},
			toRunPage(e){
				if (e.auth.auth_operate) {
					this.$router.push({
						name: "investerManageRun",
						query: { id: e.id }
					})
				} else{
					dialogConfirm("info", this.noPowerTest);
				}
			},
			deleteInvester(e){
				if (e.auth.delete_operate) {
					var message = "确定要删除该投资人吗"
					dialogConfirmHasbtn(message).then(() => {
						deleteInvesterApi({
							id: e.id
						}).then(res => {
							if(res.data.msgNo == 1) {
								dialogConfirm("success", res.data.payload);
								
								let length = this.tableData.length;
								if(length > 1){
									this.getInvesterManageList();
								}else{
									if(this.currentPage > 1){
										this.currentPage = this.currentPage - 1;
										this.getInvesterManageList();
									}else{
										this.getInvesterManageList(this.currentPage);
									}
								}
								
							} else {
								dialogConfirm("info", res.data.message);
							}
						})
					}).catch(() => {});
				} else{
					dialogConfirm("info", this.noPowerTest);
				}
			},
			// 设置权限模块开始
			setUserPower(row) {
				if (row) {
					this.is_multiple_set_power = 0;
					this.company_id = row.id;
					if(row.auth.auth_operate) {
						this.getHavePowerUsers(row.id);
					} else {
						dialogConfirm("info", "您没有设置权限，请联系管理员！");
					}
				} else{
					this.is_multiple_set_power = 1;
					this.havePowerUserIds = [];
					this.ids = '';
					this.isSetPowerDialogShow = true;
				}
			},
			getHavePowerUsers(company_id) {
				getHavePowerUsersApi({
					type: 4,
					data_id: company_id
				}).then(res => {
					console.log(res.data);
					if(res.data.msgNo == 1) {
						this.havePowerUserIds = res.data.payload.ids;
						this.ids = res.data.payload.ids.join(',');
						this.isSetPowerDialogShow = true;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			// 获取设置权限组件返回来的ids
			getSetPowerIds(idNodes) {
				console.log(idNodes);
				this.subComponentPostIds = [];
				for(let i = 0, max = idNodes.length; i < max; i++) {
					if(this.subComponentPostIds.indexOf(idNodes[i]) == -1) {
						this.subComponentPostIds.push(idNodes[i])
					}
				}
				var postIds = this.decodeSelectedUserId(this.subComponentPostIds);
				this.ids = postIds.join(',');
			},
			setPowerSubmit() {
				if (this.is_multiple_set_power) {
					if (this.multiple_set_power_ids) {
						setPowerSubmitApi({
							table_name: 'lp_user_front',
							type: 4,
							dataids: this.multiple_set_power_ids,
							ids: this.ids,
							field: 'creater_id'
						}).then(res => {
							console.log(res.data);
							if(res.data.msgNo == 1) {
								this.isSetPowerDialogShow = false;
								dialogConfirm("success", res.data.message);
							} else {
								dialogConfirm("info", res.data.message);
							}
						})
					} else{
						dialogConfirm("info", '请选择人员进行设置');
					}
				} else{
					setPowerSubmitApi({
						table_name: 'lp_user_front',
						type: 4,
						data_id: this.company_id,
						ids: this.ids,
						field: 'creater_id'
					}).then(res => {
						console.log(res.data);
						if(res.data.msgNo == 1) {
							this.isSetPowerDialogShow = false;
							dialogConfirm("success", res.data.message);
						} else {
							dialogConfirm("info", res.data.message);
						}
					})
				}
			},	
			//给加标识的员工id进行解码还原为id数组
			decodeSelectedUserId(data) {
				var user_ids = '';
				var selectedEmployeeId = [];
				var splitArr = [];
				for(var i = 0, max = data.length; i < max; i++) {
					splitArr = data[i].id.split('-');
					if(selectedEmployeeId.indexOf(splitArr[0]) == -1) {
						selectedEmployeeId.push(splitArr[0]);
					}
				}
				return selectedEmployeeId
			},
			// 设置权限模块结束
			downloadTemplet(){
				downloadTempletInvesterApi({
					
				}).then(res => {
					if(res.data.msgNo == 1) {
						window.location.href = res.data.payload;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			downloadExcel(){
				downloadExcelInvesterApi({
					
				}).then(res => {
					if(res.data.msgNo == 1) {
						window.location.href = res.data.payload.path;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			//导入Excel文件
			uploadManageCompanyInfoExcel() {
				let inputDOM = this.$refs.inputer;
				// 通过DOM取文件数据
				let file = inputDOM.files[0];
				if(file == undefined) {
					return false;
				}
				if(!/\.xl(s[xmb]|t[xm]|am|s)$/.test(file.name)) {
					dialogConfirm('info', '请选择Excel格式文件')
					inputDOM.value = '';
					return false
				}
				uploadInvestorExcelApi({},file).then(res => {
					if(res.data.msgNo == 1) {
						dialogConfirm('success', '导入成功');
						this.currentPage = 1;
						this.getInvesterManageList();
					} else {
						dialogConfirm('info', '导入失败，请联系开发人员');
					}
					inputDOM.value = '';
				})
			},
			
			
			// 分页功能模块
			changeCurrentPage(e){
				this.currentPage = e;
				this.getInvesterManageList()
			},
			everyPageSizeChange(e){
				this.pageSize = e;
				this.getInvesterManageList()
			},
			addNewInvester() {
				this.$router.push({
					name:"investerManageAdd"
				})
			},
			// 列表多选框改变事件
			handleSelectionChange(e){
				console.log(e);
				var arr = [];
				for (var i = 0,max = e.length;i<max;i++) {
					arr.push(e[i].id)
				}
				this.multiple_set_power_ids = arr.join(',');
			}
		},
		
	}
</script>

<style scoped="scoped">
	.body {
		padding: 0 30px 30px 30px;
		font-size: 14px;
	}
	.search_group_bottom{
		margin-top: 20px;
	}
	.search_group_bottom .search_box{
		width: 140px;
	}
	
	.search_box{
		width: 170px;
		height: 30px;
		margin-right: 13px;
	}
	.btn_box{
		margin-left: 24px;
	}
	.btn_box button{
		width: 80px;
		height: 30px;
		color: white;
		font-size: 14px;
		border-radius: 20px;
		background: #6292F4;
	}
	.under_fund_header {
		margin: 26px 0;
		color: #6292F4;
		font-size: 12px;
	}
	.under_fund_header>div{
		color: #6292F4;
	}
	.under_fund_header span {
		color: #6292F4;
		padding: 0 4px;
	}
	.under_fund_header button{
		/*width: 160px;*/
		height: 30px;
		margin-left: 10px;
		padding: 0 15px;
		color: #6292F4;
		font-size: 14px;
		border-radius: 20px;
		border: 1px solid #6292F4;
		/*background: #6292F4;*/
	}
	.under_fund_header button span{
		color: #6292F4;
		/*font-size: 14px;*/
	}
	.under_fund_header_btn{
		color: white !important;
		background: #6292F4 !important;
	}
	.under_fund_header_btn span{
		color: white !important;
	}
	.dialog_btn {
		width: 94px;
		height: 36px;
		color: #6292F4;
		border-radius: 20px;
		border: 1px solid #6292F4;
	}
	
	.dialog_btn_submit {
		margin-left: 20px;
		color: white !important;
		background: #6292F4;
	}
	
	.line_box {
		margin: 30px 0;
	}
	
	.line {
		height: 1px;
		background-color: #E5E5EA;
	}
</style>


